<template>
  <div class="mui-header" :class="{'is-fixed': fixed}">
    <div class="mui-header-left">
      <slot name="headerLeft" v-if="!hideLeft">
        <i class="mui mui-back" @click="leftClick"></i>
      </slot>
    </div>
    <h1 class="mui-header-center" v-html="title"></h1>
    <div class="mui-header-right">
      <slot name="headerRight" v-if="!hideRight">
        <i class="mui mui-home" @click="rightClick"></i>
      </slot>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'm-header',
  props: {
    fixed: {
      type: Boolean,
      default: true
    },
    hideLeft: {
      type: Boolean,
      default: false
    },
    hideRight: {
      type: Boolean,
      default: false
    },
    title: {
      default: ''
    },
    leftEvent: {
      type: Function,
      default: null
    },
    rightEvent: {
      type: Function,
      default: null
    }
  },
  data () {
    return {}
  },
  methods: {
    leftClick () {
      if (this.leftEvent) {
        this.leftEvent()
      } else {
        this.$router.back()
      }
    },
    rightClick () {
      if (this.rightEvent) {
        this.rightEvent()
      } else {
        // todo
        this.$toast('What to do (默认)')
      }
    }
  }
}
</script>
 
<style lang='scss'>
 .mui-header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 15px;
   height: 45px;
   font-size: 14px;
   background-color: #008FFF;
   color: #fff;
   &.is-fixed {
     position: fixed;
     top: 0;
     left: 0;
     right: 0;
   }
   .mui-header-center {
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
     text-align: center;
     line-height: 24px;
     font-size: 18px;
   }
   i {
     font-size: 24px;
   }
 }
</style>